<template>
  <div class="login">
    <div class="l-box">
      <div class="inp-box">
        <input class="scale-border" name="account" type="text" v-model="nub" placeholder="输入卡号或手机号" @focus="nubKey(true)" @blur="nubKey(false)">
        <van-icon @click="close(1)" v-if="close1" class="close-1" name="clear" />
      </div>

      <span class="scaleBorder">
      </span>
      <div class="inp-box">
        <input type="text" name="password" v-model="pwd" placeholder="输入密码">
        <van-icon @click="close(2)" v-if="close2" class="close-2" name="clear" />
      </div>
      <span class="scaleBorder" v-if="yzm">
      </span>
      <div class="inp-box" v-if="yzm">
        <input type="text" name="yanzm" placeholder="输入验证码">
        <!--<van-icon class="close-2" name="clear" />-->
        <span class="time" @click="yan">{{time}}</span>
      </div>

    </div>
    <van-button @click="shuru" class="lbtn" tag="a" size="large" type="primary">登录</van-button>
    <div class="other">
          <span>
            手机号
            <a href="#">在线申请</a>
          </span>
      <span>
            <a href="#">忘记密码</a>
          </span>
    </div>
    <div class="shade" v-if="tishi">
      <img class="icon-xq" src="../assets/img/icon_1.png" alt="">
      <span>验证码已发送至注册手机，请输入验证码</span>
    </div>
    <van-icon class="arrow" name="arrow" />
    <van-number-keyboard
      :show="showKeyboard"
      @blur="showKeyboard = false"
      @input="onInput"
      @delete="onDelete"
    />
  </div>
</template>
<script>
  import "lib-flexible";
  import { Toast } from 'vant';
  export default {
    data() {
      return {
        tishi: false,
        time: '获取验证码',
        showKeyboard: false,
        close1: false,
        close2: false,
        nub: '',
        pwd: '',
        yzm: false
      }
    },
    methods: {
      yan: function () {
        this.tishi = true;
        var that = this;
        var timer = setInterval(function () {
          if (that.time === '获取验证码') {
            that.time = 60;
          }else if (that.time > 0) {
            that.time--
          }else {
            clearInterval(timer)
            that.time = '获取验证码'
          }
          if(that.time === 58){
            that.tishi = false
          }
        }, 1000)
      },
      nubKey(status) {
        this.showKeyboard = status
      },
      onInput(value) {
        this.nub = this.nub.concat(value)
      },
      onDelete() {
        this.nub = this.nub.substr(0, this.nub.length - 1);
      },
      close(index) {
        if(index === 1){
          this.nub = ''
          this.close1 = false
        }else {
          this.pwd = ''
          this.close2 = false
        }
      },
      shuru() {
        if(this.nub === ''){
          Toast('请输入手机号(手机号请校验)');
        }else if (this.pwd === '') {
          Toast('请输密码');
        }else {
          this.yzm = true
        }
      }
    },
    watch: {
      nub: function () {
        if (this.nub !== ''){
          this.close1 = true
        }
      },
      pwd: function () {
        if (this.pwd !== ''){
          this.close2 = true
        }
      }
    }
  }
</script>
<style lang='less' scoped>
  .wh(@w, @h) {
    width: @w;
    height: @h;
  }

  .dpl {
    display: inline-block;
    vertical-align: middle;
  }

  .auto {
    left: 0;
    right: 0;
    margin: 0 auto;
  }

  .login {
    position: relative;
    background: url("../assets/img/bg.jpg") no-repeat;
    background-size: 100% 100%;
    .wh(100%,
    100%);
    .l-box {
      position: absolute;
      bottom: 427px;
      padding: 20px 40px;
      background: #fff;
      border-radius: 3px;
      box-sizing: border-box;
      font-size: 24px;
      .auto;
      .wh(644px,
      auto);
      input {
        display: block;
        width: 100%;
        height: 80px;
        line-height: 40px;
        font-size: 30px;
        outline: none;
        border: none;
        color: #666;
      }
      .inp-box {
        position: relative;
        width: 100%;
        height: 80px;
        .close-1,
        .close-2 {
          position: absolute;
          top: 30px;
          right: 0;
          width: 30px;
          height: 30px;
          z-index: 100;
          color: #b1b3b3;
        }
        .time {
          position: absolute;
          top: 30px;
          right: 30px;
          font-size: 24px;
        }
      }
      input::-webkit-input-placeholder {
        color: #bcbcbc;
      }
      .scaleBorder {
        position: relative;
        content: "";
        display: block;
        background: #eee;
        transform: scaleY(.6);
        .wh(100%,
        1px);
      }
    }
    .lbtn {
      position: absolute;
      bottom: 294px;
      background: #893dbd;
      outline: none;
      border: none;
      .auto;
      .wh(643px,
      98px)
    }
    .other {
      position: absolute;
      bottom: 235px;
      overflow: hidden;
      padding: 0 12px;
      color: #bcbcbc;
      font-size: 14px;
      box-sizing: border-box;
      .auto;
      .wh(644px,
      auto);
      span:nth-of-type(2) {
        float: right;
      }
      a {
        color: #a76fcd;
      }
    }
    .shade {
      position: absolute;
      bottom: 450px;
      background: url("../assets/img/shade.png") no-repeat;
      background-size: 100% 100%;
      font-size: 28px;
      text-align: center;
      padding: 36px 0 16px;
      box-sizing: border-box;
      .wh(661px,
      238px);
      .auto;
      .icon-xq {
        display: block;
        margin: 0 auto 34px;
        .wh(64px,
        64px)
      }
      span {
        display: block;
        font-size: 28px;
        color: #000;
      }
    }
    .arrow {
      position: absolute;
      bottom: 25px;
      transform: rotate(-90deg);
      color: #893dbd;
      font-size: 34px;
      .auto;
      .wh(17px,
      12px)
    }
  }
</style>



